import {
  FooterWrp,
  Container,
  NavContainer,
  Title,
  Txt,
  Address,
  Link,
  Logo,
  LogoIcon,
  SocLinksContainer,
  SocLink,
  InstagramIcon,
  FacebookIcon,
  YoutubeIcon,
  TelegramIcon,
  SocDescWrapper,
  SocLinksContainerDesc,
  LinksContainerDesc,
} from "./Footer.styled";

import youtubeLinkDesc from "../../assets/img/Footer-soc-links/youtube-link-desc.png";
import instagramLinkDesc from "../../assets/img/Footer-soc-links/instagram-link-desc.png";
import facebookLinkDesc from "../../assets/img/Footer-soc-links/facebook-link-desc.png";
import telegramLinkDesc from "../../assets/img/Footer-soc-links/telegram-link-desc.png";

const Footer = () => {
  return (
    <FooterWrp>
      <Container>
        <NavContainer>
          <Link href="#">
            <Title></Title>
          </Link>
          <Link href="#features"> </Link>
          <Link href="#advantages"></Link>
          <Link href="#gallery"></Link>
          <Link href="#infrastructure"></Link>
          <Link href="#location"></Link>
        </NavContainer>
        <NavContainer>
          <Title></Title>
          <Link href="#consultation"> </Link>
          <Link href="#location">  </Link>
        </NavContainer>
        <SocDescWrapper>
          <NavContainer>
            <Address>
                : ., .  12
            </Address>
            <Txt> :  -   09:00  18:00</Txt>
            <Txt> :  -    </Txt>
            <Txt> : +380981194159</Txt>
          </NavContainer>
          <SocLinksContainerDesc>
            <Txt> :</Txt>
            <LinksContainerDesc>
              <Link>
                <img src={youtubeLinkDesc} alt="youtube link" />
              </Link>
              <Link>
                <img
                  style={{ marginTop: "16px" }}
                  src={instagramLinkDesc}
                  alt="instagram link"
                />
              </Link>
              <div
                style={{ display: "flex", columnGap: "12px", marginTop: "6px" }}
              >
                <Link>
                  <img src={facebookLinkDesc} alt="facebock link" />
                </Link>
                <Link>
                  <img src={telegramLinkDesc} alt="telegram link" />
                </Link>
              </div>
            </LinksContainerDesc>
          </SocLinksContainerDesc>
        </SocDescWrapper>
        <Logo>
          <LogoIcon />
          <span>Nice View</span>
        </Logo>
      </Container>
      <SocLinksContainer>
        <SocLink>
          <InstagramIcon />
        </SocLink>
        <SocLink>
          <FacebookIcon />
        </SocLink>
        <SocLink>
          <YoutubeIcon />
        </SocLink>
        <SocLink>
          <TelegramIcon />
        </SocLink>
      </SocLinksContainer>
    </FooterWrp>
  );
};

export default Footer;
